import React from 'react';
import Icon24BrowserBack from '@vkontakte/icons/dist/24/browser_back';
import Icon24MoreVertical from '@vkontakte/icons/dist/24/more_vertical';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import Icon16Clear from '@vkontakte/icons/dist/16/clear';
const profile = ({ id, this: love}) => (
    <Panel id={id}>
        <div className="profile">
        <div className="titlesbiography"  onClick={() => love.goBack()}>
            <Icon24BrowserBack width={30} height={30} style={{display: "inline-block"}}/> Назад

        </div>
        <br/>
        <br/>
        {love.state.booksfavorite_array.length === 0 ? 
        
        love.state.bookshistory_array.length === 0 ? <div className="errorprofile">У вас нет избранных и истории книг!</div> : null
        : null}
        
        {console.log(love.state.booksfavorite_array.length)}
        {love.state.booksfavorite_array.length === 0 ? 
null 
        :         <div className="history">
        <div className="titless">
            Избранное
        </div>
        </div> }
                <br/>
            {love.state.booksfavorite_array.map(function(result, i) {
                            return (
                                <div key={i}>
                                <div className="allbooks">
                                    <img onClick={() => love.goBook(result["id"])} alt="обложка" src={result['cover']}/>
                                    <div onClick={() => love.goBook(result["id"])} className="content">
                                    <div className="contenttitle">{result['name']}</div>
                                    <div className="contentreyt">{result['rating']}</div>
                                    <div className="contentauthor">{(result['authors']['0'].first_name)} {(result['authors']['0'].last_name)}</div>
                                    <div className="contentviews">
                                        Просмотров: {result['views']}
                                    </div>
                                </div>
                                <div className="bottonfunctions" onClick={() => {love.setState({formodal_title: result['name'], formodal_id: result['id'], }); love.openModalSettings(result['id'])}}><Icon24MoreVertical/></div>
                        </div>
                        </div>
                            )
                    })}
<br/>
{love.state.bookshistory_array.length === 0 ? 
null 
        :         <div className="history">
        <div className="titless">
            История <Icon16Clear width={20} height={20} className="clearhistory" onClick={() => love.deleteHistory()}/>
        </div>
        </div>   }
            {love.state.bookshistory_array.map(function(result, i) {
                            return (
                                <div key={i}>
                                <div className="allbooks">
                                    <img onClick={() => love.goBook(result["id"])} alt="обложка" src={result['cover']}/>
                                    <div onClick={() => love.goBook(result["id"])} className="content">
                                    <div className="contenttitle">{result['name']}</div>
                                    <div className="contentreyt">{result['rating']}</div>
                                    <div className="contentauthor">{(result['authors']['0'].first_name)} {(result['authors']['0'].last_name)}</div>
                                    <div className="contentviews">
                                        Просмотров: {result['views']}
                                    </div>
                                </div>
                                <div className="bottonfunctions" onClick={() => {love.setState({formodal_title: result['name'], formodal_id: result['id'], }); love.openModalSettings(result['id'])}}><Icon24MoreVertical/></div>
                        </div>
                        </div>
                            )
                    })}
        </div>
    </Panel>
);

export default profile;